<template>
  <div>
    <div class="row mb-4">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Create Tabs</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
          <router-link to="/admin/tabs">
            <v-btn color="error">
              <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
          </router-link>
        </div>
    </div>
    <template>
      <div class="card">
        <div class="card-body my-4">
          <v-form @submit.prevent="formsubmit" ref="form" v-model="valid" lazy-validation>
            <v-text-field v-model="formData.title" :counter="10" :rules="titleRules" label="Tab Title"
                          required>
            </v-text-field>

            <v-autocomplete :items="categories" item-text="cat_name" item-value="id" label="Select Category" v-model="formData.category" @change="getSelectedSubcategory()" dense :rules="categoryRules"></v-autocomplete>

            <v-autocomplete :items="subcategories" item-text="subcat_name" multiple chips item-value="id" label="Select Sub category" v-model="formData.subcategory" dense :no-data-text="customNoDataText" :rules="subcategoryRules"></v-autocomplete>

            <v-btn type="submit" :disabled="!valid" color="success" class="mr-4" >
              SAVE
            </v-btn>
            <router-link to="/admin/tabs">
              <v-btn color="error" class="mr-4" @click="reset">
                CANCEL
              </v-btn>
            </router-link>
          </v-form>
          <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
            <template v-slot:action="{attrs}">
              <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
            </template>
          </v-snackbar>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import axios from "axios";
export default {
    name: "CrateTabs",
    components: {  },
    data(){
      return{
        valid: true,
        switch1: true,
        switch2: true,
        categories:[],
        subcategories:[],
        snackbar: false,
        text: '',
        timeout: '',
        customNoDataText: 'Please Select Category first!',
        formData:{
          title : '',
          category : '',
          subcategory : '',
        },
        titleRules: [
                v => !!v || 'Required.',
        ],
        categoryRules: [
                v => !!v || 'Required.',
        ],
        subcategoryRules: [
                v => !!v || 'Required.',
        ],
      }
    },

    created(){
      this.getCategoryAndSubcategories();
    },
    methods: {
      formsubmit(){

        var data = {
          title : this.formData.title,
          category : this.formData.category,
          subcategory : this.formData.subcategory,
        };

        axios.post('add/tab', data)
          .then((resp) => {
            this.snackbar = true;
            this.text = resp.data.message;
            this.timeout = 3000;
            this.$refs.form.reset();
          });

        this.$refs.form.validate();
      },
        // To get categories and subcategories ( code start )
        getCategoryAndSubcategories(){
          axios.get("categories/subcategories").then( (resp) =>{
            this.categories = resp.data.categories;
          });
        },
        // To get categories and subcategories ( code end )
        
        // To get categories and subcategories ( code start )
        getSelectedSubcategory(){
          axios.get("subcategories/"+this.formData.category).then( (resp) =>{
            this.subcategories = resp.data;
          });
        },
        // To get categories and subcategories ( code end )
    },
}
</script>